import React, { Component, Fragment } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import BtnSave from 'components/common/button/BtnSave';
import BtnCancel from 'components/common/button/BtnCancel';
import { Row, Card, Col, Form, Input, Checkbox, message } from 'antd';
import { BtnDelete, BtnAddNew } from 'components/common/button/index';
import {
  YearSemesterInput,
  SelectAjax,
  TableDataEditRow,
  DatePickerString,
} from 'components/common/index';
import { getPrizeClassification } from 'services/prizeTypeForAddScholarship';
import { getCodeStudent, createprizeNotes } from 'services/addPrizeNote';

@Form.create({ name: 'formAddScholarship' })
class FormAddScholarship extends Component {
  state = {
    studentId: '',
    initialValue: {},
    dataAddNewScholarship: [],
    pnDetails: [],
    isChecked: false,
  };

  componentDidMount = () => {
    const { match } = this.props;
    if (match.params.id) {
      this.getAddScholarshipById(match.params.id);
    }
  };

  onChange = e => {
    const { value } = e.target;
    this.getInputCode(value);
  };

  getInputCode = async (e, index) => {
    try {
      const { dataAddNewScholarship } = this.state;
      const { value } = e.target;
      if (value.trim().length > 0) {
        try {
          const dataStudentCode = await getCodeStudent(value);
          const data = dataAddNewScholarship.map((item, indexItem) => {
            if (indexItem === index) {
              item.key = new Date().getTime();
              item.code = dataStudentCode.data.code;
              item.fullname = `${dataStudentCode.data.firstname} ${dataStudentCode.data.lastname}`;
              item.dob = dataStudentCode.data.dob;
              item.className = dataStudentCode.data.className;
            }
            return {
              key: item.key,
              code: item.code,
              fullname: item.fullname,
              dob: item.dob,
              className: item.className,
            };
          });
          await this.setState({ dataAddNewScholarship: data });
        } catch (error) {
          message.warning('MSSV không đúng!');
        }
      }
    } catch (err) {
      // console.log(err);
    }
  };

  getAddScholarshipById = async id => {
    this.setState({ studentId: id });
  };

  getDataAddToTable = () => ({ data: { items: this.state.dataAddNewScholarship } });

  addRowDataTable = () => {
    const { dataAddNewScholarship } = this.state;
    const item = {
      key: new Date().getTime(),
      code: '',
      fullname: '',
      dob: '',
      className: '',
    };
    dataAddNewScholarship.push(item);
    this.setState({
      dataAddNewScholarship,
    });
  };

  deleteRowDataTable = async key => {
    const { dataAddNewScholarship } = this.state;
    const data = dataAddNewScholarship.filter(item => item.key !== key);
    await this.setState({
      dataAddNewScholarship: data,
      reload: this.state.reload + 1,
    });
  };

  handleCreateScholarship = async data => {
    await createprizeNotes(data);
    this.props.history.goBack();
    message.success('Thêm thành công!');
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      const { pnDetails } = this.state;
      if (err) {
        return;
      }
      if (pnDetails.length === 0) {
        message.warning('Vui lòng thêm học bổng!');
      }
      if (pnDetails.length > 0) {
        const data = { id: null, ...values, pnDetails };
        this.handleCreateScholarship(data);
      }
    });
  };

  handleSaveRowData = async rowData => {
    const { objSelected, code, solutionId, amount, isCorp } = rowData;
    this.setState({ isChecked: isCorp });
    const dataDetails = {
      studentId: code,
      solutionId,
      amount,
      isCorp,
    };
    await this.setState({ pnDetails: [dataDetails] });
    // console.log('pnDetails', this.state.pnDetails);
    if (!solutionId || !amount) {
      message.error('Vui lòng điền thông tin');
      return null;
    }

    return {
      ...rowData,
      solutionId: objSelected.solutionId.label,
      amount,
      isCorp,
    };
  };
  render() {
    const { initialValue, studentId } = this.state;
    const { getFieldDecorator } = this.props.form;
    const columns = [
      {
        title: 'Mã sinh viên',
        dataIndex: 'code',
        key: 'code',
        render: (text, record, index) => (
          <Input
            placeholder="Nhập MSSV"
            allowClear
            defaultValue={record.code}
            maxLength={10}
            onChange={this.onChange}
            onBlur={e => this.getInputCode(e, index)}
          />
        ),
      },
      {
        title: 'Họ tên',
        dataIndex: 'fullname',
        key: 'fullname',
      },
      {
        title: 'Ngày sinh',
        dataIndex: 'dob',
        key: 'dob',
      },
      {
        title: 'Lớp',
        dataIndex: 'className',
        key: 'className',
      },
      {
        title: 'Loại học bổng',
        dataIndex: 'solutionId',
        width: '150px',
        key: 'solutionId',
        editable: true,
        renderFormItemEdit: ({ form, record }) => (
          <Form.Item>
            {form.getFieldDecorator('solutionId', {
              initialValue: record.solutionId,
            })(<SelectAjax form={form} service={getPrizeClassification} allowGetObjSelected />)}
          </Form.Item>
        ),
      },
      {
        title: 'Số tiền',
        dataIndex: 'amount',
        key: 'amount',
        editable: true,
        renderFormItemEdit: ({ form, record }) => (
          <Form.Item>
            {form.getFieldDecorator('amount', {
              initialValue: record.amount,
            })(<Input />)}
          </Form.Item>
        ),
      },
      {
        title: 'Học bổng doanh nghiệp',
        dataIndex: 'isCorp',
        key: 'isCorp',
        editable: true,
        render: () => <Checkbox checked={this.state.isChecked} disabled />,
        renderFormItemEdit: ({ form, record }) => (
          <Form.Item>
            {form.getFieldDecorator('isCorp', {
              valuePropName: 'checked',
              initialValue: record.isPrize,
            })(<Checkbox />)}
          </Form.Item>
        ),
      },
    ];
    return (
      <Card title={studentId ? 'Cập nhật học bổng' : 'Thêm học bổng'}>
        <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSubmit}>
          <Row gutter={24}>
            <Col span={6}>
              <Form.Item label="Số QĐ" hasFeedback>
                {getFieldDecorator('approvalNumber', {
                  initialValue: initialValue.approvalNumber,
                  rules: [
                    {
                      required: true,
                      message: 'Vui lòng nhập thông tin!',
                    },
                  ],
                })(<Input />)}
              </Form.Item>
            </Col>
            <Col span={6}>
              <Form.Item label="Ngày QĐ" hasFeedback>
                {getFieldDecorator('approvalDate', {
                  initialValue: initialValue.approvalDate,
                  rules: [
                    {
                      required: true,
                      message: 'Vui lòng nhập thông tin!',
                    },
                  ],
                })(<DatePickerString />)}
              </Form.Item>
            </Col>
            <Col span={6}>
              <Form.Item label="Nội dung" hasFeedback>
                {getFieldDecorator('description', {
                  initialValue: initialValue.description,
                  rules: [
                    {
                      required: true,
                      message: 'Vui lòng nhập thông tin!',
                    },
                  ],
                })(<Input />)}
              </Form.Item>
            </Col>
            <YearSemesterInput form={this.props.form} />
            <Col span={6}>
              <div className="btn-group">
                <BtnSave htmlType="submit" />
                <BtnCancel onClick={() => this.props.history.goBack()} />
              </div>
            </Col>
          </Row>
        </Form>
        <Row>
          <BtnAddNew title="Thêm học bổng" onClick={this.addRowDataTable} />
        </Row>
        <Row>
          <Col span={24}>
            <TableDataEditRow
              reload={this.state.reload}
              paramSearch={this.state.paramSearch}
              service={this.getDataAddToTable}
              columns={columns}
              handleSaveRowData={this.handleSaveRowData}
              customBtnAction={({ record, btnAction }) => {
                if (record.disable) {
                  return null;
                }
                return (
                  <Fragment>
                    {btnAction}
                    <BtnDelete onClick={() => this.deleteRowDataTable(record.key)}>Xóa</BtnDelete>
                  </Fragment>
                );
              }}
              rowKey="key"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormAddScholarship {...props} />
  </Page>
));



